body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


:root {
  /* Color palette */
  --bg: #FFFF;
  --surface: #0b1220;
  --muted: #2b2f35;
  --accent: #0647d4;
  --accent-2: #7c3aed;
  --text: #000000;
  --card-bg: #ffffff;
  --border: rgba(54, 43, 205, 0.06);
  --section-gap: 32px;

  font-size: 20px;
}

.no-pad {
  padding: 0px;
  margin: 0px;
}

p {
  font-size: 20px;
}

.fbig {
  font-size: 25px;
  font-weight: bold;
}

.fsmall {
  font-size: 15px;
}

/* Page basics */
.main-page {
  min-height: 100vh;
  background: var(--bg);
  color: var(--text);
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial;
}

.container {
  width: min(1200px, 92%);
  margin: 0 auto;
  padding: 40px 0;
}

.card {
  background: linear-gradient(180deg, rgba(255,255,255,0.02), rgba(255,255,255,0.01));
  border: 5px solid var(--border);
  border-radius: 8px;
  /* box-shadow: 0 6px 18px rgba(2,6,23,0.6); */
  min-width: 300px;
}

.no-shadow {
  box-shadow: none!;
}

.section {
  width: 100%;
  padding: 24px;
  margin-bottom: var(--section-gap);
}

.section-title {
  font-size: 20px;
  font-weight: 700;
  color: var(--accent);
  margin-bottom: 12px;
}

.subtitle {
  color: var(--muted);
  margin-bottom: 8px;
}

.members-table {
  width: 100%;
  border-collapse: collapse;
}
.members-table th,
.members-table td {
  padding: 10px 12px;
  border-bottom: 1px solid rgba(255,255,255,0.03);
  text-align: left;
}
.members-table thead th {
  color: var(--accent-2);
  font-weight: 700;
}

.two-column {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
}

.image {
  width: 50vw;
  min-height: 70vh;
  max-height: 420px;
  object-fit: contain;
  display: block;
  margin: 12px 0 0 0;
  border-radius: 6px;
  background: rgba(0,0,0,0.1);
  border: 1px solid rgba(255,255,255,0.03);

  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
}

.muted-box {
  background: rgba(255,255,255,0.02);
  padding: 12px;
  border-radius: 6px;
  color: var(--muted);
}

.section h3 {
  margin-top: 0;
}

.note {
  color: var(--muted);
}

.conclusion {
  line-height: 1.5;
}

.nb {
  width: 100%;
  height: 640px;
  border: none;
  border-radius: 6px;

  overflow-y: scroll;
}

.right-align {
  text-align: right;
}

@media (max-width: 880px) {
  .two-column { grid-template-columns: 1fr; }
  .container { padding: 24px 0; }
}

.center {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.title {
  font-size: 20px;
  font-weight: bold;
}

td {
  border-right: 1px solid black;
  border-left: 1px solid black;
  text-align: center;

  margin-left: 20px;
}

.flex-row-no-pad {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
}

li {
  font-size: inherit;
}

.flex-row {
  display: flex;
  flex-direction: row;
  padding : 25px;
  margin: 25px;

  justify-content: space-evenly;
  align-items: center;
}

.flex-col {
  display: flex;
  flex-direction: column;
  padding : 25px;
  margin: 25px;

  justify-content: space-evenly;
  align-items: center;
}

.distance {
  margin-left: 50px;
  margin-right: 50px;
}

.icon {
  height: 80px;
}

.mouse-hand {
  cursor: pointer;
}

.wide-img {
  min-height: 50vh;
  width : 85vw;
}

