.formula {
  font-family: 'Times New Roman', serif;
  background: #f7f7f9;
  padding: 10px;
  border-left: 4px solid #e1e1e8;
  margin: 12px 0;
}
.formula-inline {
  font-family: 'Times New Roman', serif;
  color: #0b5394;
  padding: 2px 6px;
  border-radius: 4px;
  background: rgba(11,83,148,0.07);
  font-size: 1.05em;
}
/* Block-style formula for larger displayed equations */
.formula-block {
  font-family: 'Times New Roman', serif;
  background: #f7f7f9;
  padding: 14px 18px;
  border-left: 5px solid #0b5394;
  margin: 16px 0;
  font-size: 1.08em;
  line-height: 1.6;
  border-radius: 6px;
}
.matrix-title {
  font-weight: 600;
  margin-bottom: 6px;
}
.confusion-matrix .matrix-block {
  display: inline-block;
  margin: 8px;
  vertical-align: top;
}
.color-dist-images img {
  max-width: 180px;
  margin: 6px;
}
