.code {
    display: block;
    font-family: monospace;
    unicode-bidi: isolate;
    white-space-collapse: preserve;
    text-wrap-mode: wrap;
    text-wrap-style : stable;
    margin-block: 1em 1em;
    margin-inline: 0px;

    background-color: rgba(0, 0, 0, 0.1);
    color : #000;

    font-family: monospace;
    font-size: 14px;
}

.codeBright {
    font-family: monospace;
    font-size: 14px;
}

.cover {
    background-color: rgba(0, 0, 0, 0.1);
    justify-content: space-evenly;
    align-items: start;
    text-align: start;
}

.spaces {
    width : 4em;
    height : 1em;
}

.code-row {
    width : fit-content
}
